import React, { useState, useEffect } from 'react';
import './Notice.css'; // CSS  import
import { Link } from "react-router-dom";
import waitline from './assets/waitline.png'; //  import

const MIN_SPEED = 1.5;
const MAX_SPEED = 2.5;

function randomNumber(min, max) {
  return Math.random() * (max - min) + min;
}

class Blob {
  constructor(el) {
    this.el = el;
    const boundingRect = this.el.getBoundingClientRect();
    this.size = boundingRect.width;
    this.initialX = randomNumber(0, window.innerWidth - this.size);
    this.initialY = randomNumber(0, window.innerHeight - this.size);
    this.el.style.top = `${this.initialY}px`;
    this.el.style.left = `${this.initialX}px`;
    this.vx = randomNumber(MIN_SPEED, MAX_SPEED) * (Math.random() > 0.5 ? 1 : -1);
    this.vy = randomNumber(MIN_SPEED, MAX_SPEED) * (Math.random() > 0.5 ? 1 : -1);
    this.x = this.initialX;
    this.y = this.initialY;
  }

  update() {
    this.x += this.vx;
    this.y += this.vy;
    if (this.x >= window.innerWidth - this.size) {
      this.x = window.innerWidth - this.size;
      this.vx *= -1;
    }
    if (this.y >= window.innerHeight - this.size) {
      this.y = window.innerHeight - this.size;
      this.vy *= -1;
    }
    if (this.x <= 0) {
      this.x = 0;
      this.vx *= -1;
    }
    if (this.y <= 0) {
      this.y = 0;
      this.vy *= -1;
    }
  }

  move() {
    this.el.style.transform = `translate(${this.x - this.initialX}px, ${this.y - this.initialY}px)`;
  }
}

function Notice() {
  const [modalVisible, setModalVisible] = useState(false);
  const [currentNotice, setCurrentNotice] = useState('');
  const [description, setDescription] = useState('');
  const [clickedButton, setClickedButton] = useState(null);

  useEffect(() => {
    const initBlobs = () => {
      const blobEls = document.querySelectorAll('.bouncing-blob');
      const blobs = Array.from(blobEls).map((blobEl) => new Blob(blobEl));

      function update() {
        requestAnimationFrame(update);
        blobs.forEach((blob) => {
          blob.update();
          blob.move();
        });
      }

      requestAnimationFrame(update);
    };

    initBlobs();
  }, []);

  const openModal = (notice, buttonIndex) => {
    let descriptionText = "";
    switch(notice) {
      case "    ":
        descriptionText = '    \n ( CU,   24) \n    .';
        break;
      case "  ":
        descriptionText = `
        
           
        - 22 10:00-17:00
        - 23 10:00-16:00

        
           
        - 22  18:00-24:00
        - 23  18:00-02:00()

          /
        :  
          
        - 23  10:00 ~ 16:00
        `;
        break;
      case "  ":
        descriptionText = `
           5 22, 5 23  
        
             .

         5/22-23(-)
           : 14:00 ~  
         // (1)   
        
        .`;
        break;
      case "    ":
        descriptionText = `
           5 22, 5 23  
        
             .

             .
             
        
         5/22-23(-)
         :  02:00
        
        .`;
        break;
      case "   ":
        descriptionText = "";
        break;

      case "  ":
        descriptionText = `
              

        
            
        (ex.  / DSLR )


            
        (ex.  )


           ,   
           .
        
        `;
        break;

        case "2024  ":
        descriptionText = `
          BLUE&WHITE  .


        `;
        break;

        case " ":

        break;

      default:
        descriptionText = "  .";
    }
    
    setCurrentNotice(notice);
    setDescription(descriptionText);
    setClickedButton(buttonIndex);
    setModalVisible(true);
  };

  const closeModal = () => {
    setModalVisible(false);
  };

  const handleDetailInfoClick = () => {
    window.location.href = 'https://www.instagram.com/festival_cuk/';
  };

  return (
    <div className="App">
      <div className="bouncing-blobs-container">
        <div className="bouncing-blobs-glass"></div>
        <div className="bouncing-blobs">
          <div className="bouncing-blob bouncing-blob--blue"></div>
          <div className="bouncing-blob bouncing-blob--blue"></div>
          <div className="bouncing-blob bouncing-blob--blue"></div>
          <div className="bouncing-blob bouncing-blob--white"></div>
          <div className="bouncing-blob bouncing-blob--purple"></div>
          <div className="bouncing-blob bouncing-blob--purple"></div>
          <div className="bouncing-blob bouncing-blob--pink"></div>
        </div>
      </div>
      <div className="hero"></div>

      {!modalVisible && (
        <div className="vertical-buttons">
          <button className={`rectangle-button ${clickedButton === 7 ? 'clicked' : ''}`} onClick={() => openModal("2024  ", 1)}>2024  </button>
          <button className={`rectangle-button ${clickedButton === 1 ? 'clicked' : ''}`} onClick={() => openModal("    ", 1)}>    </button>
          <button className={`rectangle-button ${clickedButton === 2 ? 'clicked' : ''}`} onClick={() => openModal("  ", 2)}>  </button>
          <button className={`rectangle-button ${clickedButton === 3 ? 'clicked' : ''}`} onClick={() => openModal("  ", 3)}>  </button>
          <button className={`rectangle-button ${clickedButton === 4 ? 'clicked' : ''}`} onClick={() => openModal("    ", 4)}>    </button>
          <button className={`rectangle-button ${clickedButton === 5 ? 'clicked' : ''}`} onClick={() => openModal("   ", 5)}>   </button>
          <button className={`rectangle-button ${clickedButton === 6 ? 'clicked' : ''}`} onClick={() => openModal("  ", 6)}>  </button>
          <button className={`rectangle-button ${clickedButton === 7 ? 'clicked' : ''}`} onClick={handleDetailInfoClick}> </button>
        </div>
      )}
      {!modalVisible && (
        <Link to="/" className="link">
          <button className="back-to-top" onClick={() => window.scrollTo(0, 0)}>BACK</button>
        </Link>
      )}

      {modalVisible && (
        <div className="modal" onClick={closeModal}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-head">
              <h2>{currentNotice}</h2>
              <div className="underline"></div>
            </div>
            <div className="modal-body" style={{ whiteSpace: 'pre-line' }}>
              {description}
              {currentNotice === "   " && (
                <div>
                  <img src={waitline} alt="Waitline" className="modal-image" />
                  <p className='c'><span className='text_red'></span> :   (14:00 )</p>   
                  <p className='c'><span className='text_blue'></span> :   (18:00 )</p>
                  


                </div>
              )}
            </div>
            <button className="backbtn" onClick={closeModal}> </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default Notice;
